export default "@import '../../themes/src/default.css';\n@import './vars.css';\n\n.component {\n    width: var(--calendar-width);\n    height: var(--calendar-height);\n    display: flex;\n    flex-direction: column;\n    overflow: hidden;\n    outline: none;\n\n    @media (max-width: 374px) {\n        align-items: center;\n        width: 100%;\n        height: 321px;\n    }\n\n    &.responsive {\n        width: 100%;\n\n        & .period {\n            width: 100%;\n        }\n\n        & .monthYear {\n            width: 100%;\n        }\n\n        & .container {\n            width: 100%;\n        }\n    }\n}\n\n.container {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    padding: 0 var(--gap-m) var(--gap-m);\n    box-sizing: border-box;\n    flex-grow: 1;\n    overflow: hidden;\n\n    &.customScrollbar {\n        @mixin custom-scrollbar;\n\n        display: block;\n    }\n\n    @media (max-width: 374px) {\n        width: 288px;\n    }\n}\n\n.sixWeeks {\n    height: 388px;\n\n    @media (max-width: 374px) {\n        height: 361px;\n    }\n}\n\n.period {\n    width: var(--calendar-inner-width);\n\n    @media (max-width: 374px) {\n        justify-content: space-between;\n        width: var(--calendar-mobile-inner-width);\n        margin: 0 auto;\n    }\n}\n\n.monthYear {\n    width: var(--calendar-inner-width);\n\n    @media (max-width: 374px) {\n        justify-content: space-between;\n        width: var(--calendar-mobile-inner-width);\n        margin: 0 auto;\n    }\n}\n";