export default ":root {\n    --calendar-height: 344px;\n    --calendar-width: 344px;\n    --calendar-inner-width: 280px;\n    --calendar-mobile-inner-width: 256px;\n    --calendar-inner-third-part-width: calc(var(--calendar-inner-width) / 3);\n    --calendar-mobile-inner-half-width: calc(var(--calendar-mobile-inner-width) / 2);\n\n    /* Кнопки выбора месяцев и годов */\n    --calendar-select-button-gap: calc(var(--gap-xs) / 3);\n    --calendar-mobile-select-button-gap: calc(var(--gap-2xs) / 2);\n    --calendar-mobile-select-button-width: calc(\n        var(--calendar-mobile-inner-half-width) - var(--calendar-mobile-select-button-gap)\n    );\n    --calendar-select-button-width: calc(\n        var(--calendar-inner-third-part-width) - var(--calendar-select-button-gap)\n    );\n    --calendar-select-button-today-border-color: var(--color-light-border-key);\n    --calendar-select-button-selected-background: var(--color-light-bg-primary-inverted);\n\n    /* Шапка */\n    --calendar-month-only-header-font-family: var(--font-family-system);\n    --calendar-month-only-header-font-size: 18px;\n    --calendar-month-only-header-font-weight: 500;\n\n    /* День */\n    --calendar-day-size: 40px;\n    --calendar-day-border-radius: var(--border-radius-pill);\n\n    /* today */\n    --calendar-today-color: var(--color-light-text-link);\n\n    /* highlighted */\n    --calendar-highlighted-color: var(--color-light-text-primary);\n    --calendar-highlighted-background: var(--color-light-graphic-quaternary);\n\n    /* holidays */\n    --calendar-holiday-color: var(--color-light-text-accent);\n\n    /* range */\n    --calendar-range-color: var(--color-light-text-primary);\n    --calendar-range-background: var(--color-light-specialbg-component);\n    --calendar-range-complete-background: var(--color-light-graphic-link-alpha-15);\n\n    /* selected */\n    --calendar-selected-color: var(--color-light-text-primary-inverted);\n    --calendar-selected-background: var(--color-light-graphic-primary);\n    --calendar-selected-hover-background: var(--color-light-graphic-primary-tint-15);\n\n    /* disabled */\n    --calendar-disabled-color: var(--color-light-text-tertiary);\n    --calendar-disabled-background: transparent;\n\n    /* marker */\n    --calendar-marker-background: var(--color-light-graphic-primary);\n    --calendar-marker-selected-background: var(--color-light-graphic-primary-inverted);\n}\n";